import React, { Component } from 'react';
import { Route, Redirect } from 'react-router'

class Search extends Component {
    constructor(props) {
        super(props);
        console.log(this.props);
        this.state = {
            location: this.props.searchTerm || '',
        }
        if (this.state.location !== '') this.handleSearch();
    }

    handleSearch = (e) => {
        fetch('/api/search/' + this.state.location, { mode: 'cors' })
            .then((response) => {
                return response.json();
            })
            .then((response) => {
                this.props.onClick(response);
            });
        if(e) e.preventDefault();
    }

    handleSubmit = (e) => {
        //e.preventDefault();
        // console.log(this.state);
        // return (
        //     <Redirect to={{
        //         pathname: "/" + this.state.location,
        //         state: { searchTerm: this.state.location }
        //       }}/>
        // );
    }

    render() {
        return (
            <div className="row">
                <div className="col s1"></div>
                <form className="col s10" onSubmit={this.handleSubmit} action={"/" + this.state.location}>
                    <div className="row no-margin">
                        <div className="input-field col s12">
                            <input value={this.state.location} placeholder="Enter your location" id="main-search" type="text" className="validate" onChange={(e) => this.setState({ location: e.target.value })} />
                        </div>
                    </div>
                </form>
                <div className="col s1"></div>
            </div>
        );
    }
}

export default Search


