import React, { Component } from 'react';
import './style.css';

class attending extends Component {
    constructor(props) {
        super(props);
        this.state = {
            numAttending: 0,
        }
        this.getPlace();
    }

    getPlace = () => {
        var self = this;
        fetch('/api/place/' + this.props.id, { method: 'POST' })
            .then(function (res) {
                return res.json();
            })
            .then(function (resp) {
                if (!resp.notFound) {
                    self.setState({
                        numAttending: resp.people.length
                    });
                } else {
                    self.setState({
                        numAttending: 0
                    });
                }
            });
    }

    handleClick = () => {
        var self = this;
        fetch('/api/attend/' + this.props.id, { method: 'POST', credentials: 'include' })
            .then(function (res) {
                return res.json();
            })
            .then(function (res) {
                if (res.notLogged) {
                    window.location = "https://nightlife-serpa.herokuapp.com/api/logins";
                } else {
                    self.setState({
                        numAttending: res.people.length
                    });
                }
            });
    }

    render() {
        return (
            <div>
                <button className="btn waves-effect waves-light" type="submit" name="action" onClick={this.handleClick}>
                    Attending {this.state.numAttending}
                </button>
            </div>
        );
    }

    componentWillReceiveProps(nextProps) {
        this.props = nextProps;
        this.getPlace();
    }
}

export default attending;


