import React, { Component } from 'react';
import {
    BrowserRouter,
    Route,
    Link
  } from 'react-router-dom';
import Home from './ui/pages/home';

class Router extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        return (<BrowserRouter>
            <div>
                
                <Route exact path="/:searchTerm" component={Home}/>
            </div>
        </BrowserRouter>);
    }
}

export default Router;


