import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom';
import './style.css';
import bg from './bg3.jpg';
import Search from '../../components/search';
import Place from '../../components/place';

class Home extends Component {
    constructor(props) {
        console.log(props);
        super(props);
        this.state = {
            places: [],
            searchTerm: this.props.match.params.searchTerm,
        }
        console.log(this);
    }

    handleSearch = (data) => {
        this.setState({places: data});
    }

    buildPlaces = () => {
        return this.state.places.map(place => <Place {...place} />);
    }

    render() {
        return (
            <div>
                {console.log(this)}
                <Search onClick={this.handleSearch} searchTerm={this.state.searchTerm} />
                <br/>
                {this.state.places.length > 0 ? this.buildPlaces() : null}
            </div>
        );
    }
}

export default Home;


